*{
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body{
  background: #1C7430;
}

.App {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  min-height: 600px;  
}

.game {
  width: 80%;
  margin: 30px auto;
}

.board {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

.board img {
  max-width: 90%;
  max-height: 90%;
  /* margin: 20px; */
}

.img-container {
  -ms-flex: 1 1 15%;
      flex: 1 1 15%;
  /* width: 106px; */
  height: 180px;
  /* border: 1px solid black; */
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

@media screen and (max-width: 768px) {
  .img-container {
    height: 140px;
  }
}

@media screen and (max-width: 384px) {
  .img-container {
    height: 100px;
  }
}

.game-info {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  margin-bottom: 20px;
  opacity: 0.8;
  font-family: OpenSans-Semibold;
  font-size: 20px;
  color: #FFFFFF;
  /* letter-spacing: -0.25px; */
}

.start-div, .game-over-div {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
}

.start-div *, .game-over-div * {
  margin-bottom: 20px; 
}

.start-div span, .game-over-div p {
 color: white;
 font-size: 28px;
}

.start-div, .game-over-div {
  font-family: OpenSans-Bold;
  color: #1C7430;
  letter-spacing: 0;
}

.start-div button, .game-over-div button {
  font-size: 16px;
  width: 30%;
  opacity: 0.85;
  background: #FFFFFF;
  border-radius: 4px;
  height: 30px; 
}

.start-div *, .game-over-div * {
  max-width: 100%;
}


